@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Roboto");
@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,600);
html, body {
  width: 100%;
  height: 100vh;
  background-image: url("../images/background_home.jpg");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 10%;
  background: #dadada;
  box-shadow: 0px 27px 24px 0px rgba(0, 0, 0, 0.2), 0px 40px 70px 0px rgba(0, 0, 0, 0.22);
}

nav .inner-nav {
  width: 90%;
}

nav .inner-nav ul {
  display: flex;
  flow-direction: row;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

nav .inner-nav ul li a {
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  text-decoration: none;
  color: #1a1a1a;
  letter-spacing: 0;
  position: relative;
  text-transform: uppercase;
}

nav .inner-nav ul li a:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 0;
  height: 2px;
  background-color: #1a1a1a;
  content: "";
  transition: width 0.2s;
}

nav .inner-nav ul li a:hover:after {
  width: 100%;
}

.container {
  width: 80%;
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container .inner-container {
  width: 100%;
  height: 80%;
  background-color: #dadada;
  box-shadow: 0px 27px 24px 0px rgba(0, 0, 0, 0.2), 0px 40px 70px 0px rgba(0, 0, 0, 0.22);
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.container .inner-container .inner-container-left, .container .inner-container .inner-container-right .wish, .container .inner-container .inner-container-right {
  width: 40%;
  height: 80%;
}

.container .inner-container .inner-container-left, .container .inner-container .inner-container-right .wish {
  background-color: #FFF;
  box-shadow: 0px 27px 24px 0px rgba(0, 0, 0, 0.2), 0px 40px 70px 0px rgba(0, 0, 0, 0.22);
  border-radius: 5px;
}

.container .inner-container .inner-container-right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.container .inner-container .inner-container-right .wish {
  height: 30%;
  width: 100%;
}

.container .inner-container .inner-container-right .wish blockquote {
  margin: 1.5em 10px;
  padding: 0.5em 10px;
  quotes: "“" "”";
}

.container .inner-container .inner-container-right .wish blockquote:before {
  color: #ccc;
  content: open-quote;
  font-size: 4em;
  line-height: 0.1em;
  margin-right: 0.25em;
  vertical-align: -0.4em;
}

.container .inner-container .inner-container-right .wish blockquote p {
  display: inline;
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  color: #1a1a1a;
}
